import { Search, searchConfType } from "../../components/Search";
import { useState, useEffect } from "react";
import { Table, Dialog, Button, Radio, MessagePlugin } from "tdesign-react";
import { ErrorCircleFilledIcon } from "tdesign-icons-react";
import { apiFenceList, apiDeleteFence } from "../../api/api";
import NoData from "../../components/NoData";
import moment from "moment";

function Fence(props: any) {
  const [visible, setVisible] = useState(false);
  const [page, setPage] = useState(1);
  const [pageSize, setPageSize] = useState(10);
  const [Data, setData] = useState([]);
  const [total, setTotal] = useState(30);
  const [deleteDish, setDeleteDish] = useState({});
  const [provider, setProvider] = useState("BAIDU");
  const [searchValue, setSearchValue] = useState({});
  const searchConf: searchConfType[] = [
    {
      label: "围栏名称:",
      key: "fenceName",
      value: "",
      callback: searchHandle,
      setValue: setSearchValue,
    },
    {
      label: "围栏ID:",
      key: "fenceId",
      value: "",
      callback: searchHandle,
      setValue: setSearchValue,
    },
  ];

  // 生命周期调用数据
  useEffect(() => {
    getData();
  }, []);

  // 获取数据
  function getData(items?: any) {
    apiFenceList({
      page: items?.page ?? page,
      pageSize: items?.pageSize ?? pageSize,
      fenceName: items?.fenceName,
      fenceId: items?.fenceId,
      provider: items?.provider ?? provider,
    }).then((res: any) => {
      const { data } = res;
      if (res.code === 0) {
        setData(data.items);
        setTotal(data.total);
      } else {
        MessagePlugin.error("请求出错！");
      }
    });
  }

  // 搜索功能
  function searchHandle(searchVal: any) {
    setPage(1);
    setPageSize(10);
    setSearchValue(searchVal);
    getData({ page: 1, pageSize: 10, ...searchVal });
  }
  // 关闭弹框
  const handleFooterClose = () => {
    setVisible(false);
  };

  // 删除功能
  const deleteHandle = () => {
    if (deleteDish && (deleteDish as any).fenceId) {
      const {fenceId, serverId} = deleteDish as any
      apiDeleteFence({fenceId,serverId, provider}).then(res => {
        if ((res as any).code === 0){
          setDeleteDish({});
          setVisible(false);
          MessagePlugin.success('信息删除成功！');
          getData({ ...searchValue, provider })
        } else {
          MessagePlugin.error((res as any).msg);
        }
      })   
    } else {
      MessagePlugin.error("删除请求出错了");
    }
  };
  // 更改单选类型 - 不同服务商切换
  const changeHandle = (val: any) => {
    setProvider(val);
    getData({ ...searchValue, provider: val });
  };
  return (
    <div className="container fence">
      <div className="searchCont">
        <Search searchConf={searchConf}></Search>
        <div className="tdesign-demo-block-row">
          <Radio.Group value={provider} onChange={changeHandle}>
            <Radio value="BAIDU">百度地图</Radio>
            <Radio value="AMAP">高德地图</Radio>
            {/* <Radio value="TMAP">腾讯地图</Radio> */}
          </Radio.Group>
        </div>
      </div>
      <div className="tableCont">
        {Data && Data.length > 0 ? (
          <Table
            data={Data}
            columns={[
              {
                align: "left",
                className: "test",
                ellipsis: true,
                colKey: "name",
                title: "围栏名称",
                minWidth: 200,
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                colKey: "fenceId",
                title: "围栏ID",
                minWidth: 200,
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                cell({ row }) {
                  switch (row?.type) {
                    case "DISTRICT":
                      return <>行政区域</>;
                    case "CIRCLE":
                      return <>圆形</>;
                    case "POLYGON":
                      return <>多边型</>;
                    case "POLYLINE":
                      return <>线形</>;
                    default:
                      return null;
                  }
                },
                title: "围栏类型",
                minWidth: 200,
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                colKey: "serverId",
                title: "服务ID",
                minWidth: 200,
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                colKey: "created",
                title: "创建时间",
                cell({ row }) {
                  return row.created ? moment(row.created).format("YYYY-MM-DD HH:mm:ss") : '--';
                },
                minWidth: 200,
              },
              {
                align: "left",
                className: "test4",
                ellipsis: true,
                colKey: "updated",
                cell({ row }) {
                  return row.updated ? moment(row.updated).format("YYYY-MM-DD HH:mm:ss") : '--';
                },
                title: "更新时间",
                minWidth: 200,
              },
              {
                colKey: "op",
                width: 140,
                title: "操作",
                fixed: "right",
                cell(record) {
                  return (
                    <>
                      <a
                        className="link"
                        onClick={() => {
                          props.history.push({
                            pathname: `/fence/${record.row.id}`,
                            state: record.row,
                          });
                        }}
                      >
                        查看
                      </a>
                      <a
                        className="link"
                        onClick={() => {
                          setDeleteDish(record.row);
                          setVisible(true);
                        }}
                      >
                        删除
                      </a>
                    </>
                  );
                },
              },
            ]}
            rowKey="index"
            verticalAlign="top"
            bordered
            hover
            pagination={{
              defaultCurrent: page,
              defaultPageSize: pageSize,
              total,
              showJumper: true,
              onChange(pageInfo) {
                setPageSize(pageInfo.pageSize);
                setPage(pageInfo.current);
                getData();
              },
            }}
          ></Table>
        ) : (
          <NoData />
        )}
      </div>
      <Dialog
        header={
          <>
            <ErrorCircleFilledIcon style={{ color: "#ED7B2F" }} />
            <span>确定要删除这条围栏信息吗？</span>
          </>
        }
        footer={
          <>
            <Button
              theme="default"
              variant="outline"
              onClick={() => setVisible(false)}
            >
              取消
            </Button>
            <Button theme="danger" onClick={deleteHandle}>
              删除
            </Button>
          </>
        }
        visible={visible}
        onClose={handleFooterClose}
      ></Dialog>
    </div>
  );
}

export default Fence;
